.center-message {
  font-family: "Orbit";
  position: absolute;
  top: 20%;
  left: 50%;
  width: 270px;
  transform: translate(-50%, -50%);
  background-color: black;
  color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  text-align: center;
}